<template>
  <svg
      :width="size"
      :height="size"
      viewBox="0 0 100 100"
      role="img"
      :aria-label="'Pie chart representing data'"
  >
    <g>
      <path
          v-for="(slice, index) in slices"
          :key="index"
          :d="describeArc(50, 50, 45, slice.startAngle, slice.endAngle)"
          :fill="slice.color"
      />
    </g>
  </svg>
</template>

<script>
import { mapGetters } from 'vuex';
import _Services from "./_Services";

export default {
  extends: _Services,
  props: {
    size: {
      type: Number,
      default: 100,
    },
  },
  data() {
    return {
      slices: [],
    };
  },
  mounted() {
    this.parseData();
  },
  watch: {
    chartData: 'parseData',
  },
  methods: {
    parseData() {
      try {
        const dataArray = this.serviceContent
        const total = dataArray.reduce((sum, item) => sum + item.value, 0);
        let currentAngle = 0;
        const defaultColors = ["#FF6384", "#36A2EB", "#FFCE56", "#4BC0C0", "#9966FF", "#FF9F40"];
        this.slices = dataArray.map((item, index) => {
          const sliceAngle = (item.value / total) * 360;
          const slice = {
            color: item.color || defaultColors[index % defaultColors.length],
            startAngle: currentAngle,
            endAngle: currentAngle + sliceAngle,
          };
          currentAngle += sliceAngle;
          return slice;
        });
      } catch (e) {
        console.error("Invalid chartData for PieChart:", e);
        // Fallback to default slices if parsing fails
        this.slices = [
          {color: "#FF6384", startAngle: 0, endAngle: 180},
          {color: "#36A2EB", startAngle: 180, endAngle: 360}
        ];
      }
    },
    polarToCartesian(cx, cy, r, angleInDegrees) {
      const angleInRadians = ((angleInDegrees - 90) * Math.PI) / 180.0;
      return {
        x: cx + r * Math.cos(angleInRadians),
        y: cy + r * Math.sin(angleInRadians),
      };
    },
    describeArc(cx, cy, r, startAngle, endAngle) {
      const start = this.polarToCartesian(cx, cy, r, endAngle);
      const end = this.polarToCartesian(cx, cy, r, startAngle);

      const largeArcFlag = endAngle - startAngle <= 180 ? "0" : "1";

      const d = [
        "M", cx, cy,
        "L", start.x, start.y,
        "A", r, r, 0, largeArcFlag, 0, end.x, end.y,
        "Z"
      ].join(" ");

      return d;
    },
    ...mapGetters([
      // Map your Vuex getters here if needed
    ]),
  }
}
</script>

