<template>
  <div class="bar-graph-container">
    <svg
        :viewBox="`0 0 ${viewBoxWidth} ${viewBoxHeight}`"
        preserveAspectRatio="xMidYMid meet"
        role="img"
        aria-labelledby="chartTitle chartDesc"
        xmlns="http://www.w3.org/2000/svg"
        class="bar-graph-svg"
    >
      <title id="chartTitle">Financial Bar Graph</title>
      <desc id="chartDesc">Bar graph showing money spent over time with budget and due indicators.</desc>

      <!-- Axes Group -->
      <g class="axes">
        <!-- Y-Axis -->
        <line
            :x1="margins.left"
            :y1="margins.top"
            :x2="margins.left"
            :y2="viewBoxHeight - margins.bottom"
            stroke="black"
            stroke-width="1"
        />

        <!-- X-Axis -->
        <line
            :x1="margins.left"
            :y1="viewBoxHeight - margins.bottom"
            :x2="viewBoxWidth - margins.right"
            :y2="viewBoxHeight - margins.bottom"
            stroke="black"
            stroke-width="1"
        />
      </g>

      <!-- Y-Axis Tick Marks and Labels -->
      <g class="y-ticks">
        <line
            v-for="(tick, index) in yTicks"
            :key="'y-tick-' + index"
            :x1="margins.left - 5"
            :y1="yScale(tick)"
            :x2="margins.left"
            :y2="yScale(tick)"
            stroke="black"
            stroke-width="0.5"
        />
        <text
            v-for="(tick, index) in yTicks"
            :key="'y-label-' + index"
            :x="margins.left - 10"
            :y="yScale(tick)"
            :font-size="fontSize"
            fill="black"
            text-anchor="end"
            alignment-baseline="middle"
        >
          {{ Math.round(tick) }}
        </text>
      </g>

      <!-- Bars Group -->
      <g class="bars">
        <rect
            v-for="(bar, index) in bars"
            :key="index"
            :x="bar.x"
            :y="bar.y"
            :width="bar.width"
            :height="bar.height"
            :fill="bar.color"
            @mouseover="showTooltip(bar, $event)"
            @mouseleave="hideTooltip"
            :aria-label="`${bar.time}: ${currency}${bar.amount}`"
            tabindex="0"
        />
      </g>

      <!-- X-Axis Labels -->
      <g class="x-ticks">
        <text
            v-for="(tick, index) in xTicks"
            :key="'x-label-' + index"
            :x="xScale(tick) + xScale.bandwidth() / 2"
            :y="viewBoxHeight - margins.bottom + 15"
        :font-size="fontSize"
        fill="black"
        text-anchor="middle"
        alignment-baseline="hanging"
        >
        {{ tick }}
        </text>
      </g>

      <!-- Indicators Group -->
      <g class="indicators">
        <line
            v-for="(indicator, index) in indicatorLines"
            :key="'indicator-' + index"
            :x1="indicator.x1"
            :y1="indicator.y"
            :x2="indicator.x2"
            :y2="indicator.y"
            :stroke="indicator.color"
            :stroke-width="0.5"
            :stroke-dasharray="indicator.dasharray"
        />

        <!-- Indicator Labels -->
        <text
            v-for="(indicator, index) in indicatorLines"
            :key="'indicator-label-' + index"
            :x="indicator.labelX"
            :y="indicator.y + 0.5"
            :font-size="fontSize"
            fill="black"
            text-anchor="start"
            alignment-baseline="middle"
        >
          {{ indicator.label }}
        </text>
      </g>

      <!-- Legend Group -->
      <g class="legend" :transform="`translate(${margins.left}, ${margins.top - 10})`">
        <rect
            v-for="(indicator, index) in indicatorLines"
            :key="'legend-' + index"
            :x="index * 60"
            y="0"
            width="10"
            height="10"
            :fill="indicator.color"
            :stroke="indicator.color"
            :stroke-dasharray="indicator.dasharray"
        />
        <text
            v-for="(indicator, index) in indicatorLines"
            :key="'legend-label-' + index"
            :x="index * 60 + 15"
            y="10"
            :font-size="fontSize"
            fill="black"
            text-anchor="start"
            alignment-baseline="middle"
        >
          {{ indicator.label }}
        </text>
      </g>
    </svg>

    <!-- Tooltip -->
    <div
        v-if="tooltip.visible"
        :style="tooltip.style"
        class="tooltip"
    >
      {{ tooltip.content }}
    </div>
  </div>
</template>

<script>
import { scaleBand, scaleLinear } from 'd3-scale';

export default {
  name: 'XBarGraph',
  props: {
    chartData: {
      type: String,
      required: false,
      default: JSON.stringify([
        { time: 'Jan', amount: 1 },
        { time: 'Feb', amount: 3 },
        { time: 'Mar', amount: 13 },
        { time: 'Apr', amount: 14 },
        { time: 'May', amount: 15 },
        { time: 'June', amount: 17 },
        { time: 'July', amount: 21 },
        { time: 'Aug', amount: 22 },
        { time: 'Sept', amount: 23 },
        { time: 'Oct', amount: 30 },
        { time: 'Nov', amount: 39 },
        { time: 'Dec', amount: 43 }
      ]),
      // Expects a JSON string like '[{"time":"Jan","amount":1}, ...]'
      validator: function (value) {
        try {
          const data = JSON.parse(value);
          return Array.isArray(data) && data.every(item => 'time' in item && 'amount' in item);
        } catch (e) {
          return false;
        }
      }
    },
    currency: {
      type: String,
      default: '$',
    },
    indicators: {
      type: String, // Changed to String to accommodate JSON string
      required: false,
      default: JSON.stringify({
        budget: 100,
        spend: 50,
        due: 13,
        spendDue: 12,
      }),
      // Expects a JSON string like '{"budget":2000,"spend":1800,"due":200,"spendDue":2000}'
      validator: function (value) {
        try {
          const data = JSON.parse(value);
          return typeof data === 'object' && 'budget' in data && 'spend' in data && 'due' in data && 'spendDue' in data;
        } catch (e) {
          return false;
        }
      }
    },
  },
  data() {
    return {
      bars: [],
      indicatorLines: [],
      tooltip: {
        visible: false,
        content: '',
        style: {
          position: 'absolute',
          padding: '5px 10px',
          background: 'rgba(0, 0, 0, 0.7)',
          color: '#fff',
          borderRadius: '4px',
          pointerEvents: 'none',
          fontSize: '3px',
          transform: 'translate(-50%, -120%)',
          whiteSpace: 'nowrap',
        },
      },
      viewBoxWidth: 100,  // Initial width
      viewBoxHeight: 100, // Initial height
      minBarHeight: 2,    // Minimum height for bars
      margins: {
        top: 20,
        right: 20,
        bottom: 30,
        left: 40
      },
      // Define yScale and xScale as data properties
      yScale: function(){},
      xScale: function(){},
      tick: function (){},
    };
  },
  computed: {
    fontSize() {
      return this.viewBoxHeight / 30; // Adjust the divisor to control scaling
    },
    yMax() {
      const rawYMax = Math.max(
          ...this.chartDataArray.map(d => d.amount),
          ...this.getIndicatorsValues()
      );
      return rawYMax * 1.1; // Adding 10% padding
    },
    chartDataArray() {
      return typeof this.chartData === 'string' ? JSON.parse(this.chartData) : this.chartData;
    },
    xTicks() {
      return this.bars.map(bar => bar.time);
    },
    yTicks() {
      const numberOfTicks = 5; // Adjust as needed
      const tickStep = this.yMax / numberOfTicks;
      return Array.from({ length: numberOfTicks + 1 }, (_, i) => i * tickStep);
    }
  },
  mounted() {
    this.processChartData();
    this.calculateIndicators();

    // Initialize ResizeObserver
    this.resizeObserver = new ResizeObserver(entries => {
      for (let entry of entries) {
        const { width, height } = entry.contentRect;
        this.updateViewBox(width, height);
        this.processChartData();
        this.calculateIndicators();
      }
    });

    // Observe the parent container
    const parent = this.$el.parentElement;
    if (parent) {
      this.resizeObserver.observe(parent);
    }
  },
  beforeDestroy() {
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
  },
  watch: {
    chartData: 'processChartData',
    indicators: 'calculateIndicators',
  },
  methods: {
    updateViewBox(width, height) {
      // Avoid division by zero
      if (height === 0) return;

      // Set viewBox dimensions to match the container's dimensions
      this.viewBoxWidth = width;
      this.viewBoxHeight = height;
    },
    processChartData() {
      let dataArray = [];
      if (typeof this.chartData === 'string') {
        try {
          dataArray = JSON.parse(this.chartData);
        } catch (e) {
          console.error('Invalid chartData JSON:', e);
          return;
        }
      } else {
        dataArray = this.chartData;
      }

      if (dataArray.length === 0) {
        console.warn('No data provided for chartData.');
        return;
      }

      // Determine yMax with some padding (10%)
      const rawYMax = Math.max(...dataArray.map(d => d.amount), ...this.getIndicatorsValues());
      const yMax = rawYMax * 1.1; // Adding 10% padding

      // Define scales based on dynamic viewBox and margins
      this.xScale = scaleBand()
          .domain(dataArray.map(d => d.time))
          .range([this.margins.left, this.viewBoxWidth - this.margins.right])
          .padding(0.3); // Increased padding to make bars thinner

      this.yScale = scaleLinear()
          .domain([0, yMax])
          .range([this.viewBoxHeight - this.margins.bottom, this.margins.top]); // Inverted y-axis for SVG

      // Map data to bars with minimum height
      this.bars = dataArray.map(d => ({
        x: this.xScale(d.time),
        y: Math.min(this.yScale(d.amount), this.viewBoxHeight - this.margins.bottom - this.minBarHeight),
        width: this.xScale.bandwidth(),
        height: Math.max(this.viewBoxHeight - this.margins.bottom - this.yScale(d.amount), this.minBarHeight),
        color: this.getBarColor(d.amount),
        amount: d.amount,
        time: d.time,
      }));
    },
    calculateIndicators() {
      let indicatorsObj = {};
      if (typeof this.indicators === 'string') {
        try {
          indicatorsObj = JSON.parse(this.indicators);
        } catch (e) {
          console.error('Invalid indicators JSON:', e);
          return;
        }
      } else {
        indicatorsObj = this.indicators;
      }

      const yScale = this.yScale; // Use the yScale defined in data

      this.indicatorLines = [
        {
          label: 'Budget',
          value: indicatorsObj.budget,
          color: '#00FF00', // Green
          dasharray: '5,5',
          y: yScale(indicatorsObj.budget),
          x1: this.margins.left,
          x2: this.viewBoxWidth - this.margins.right,
          labelX: this.viewBoxWidth - this.margins.right - 225, // Position labels slightly outside the indicators
        },
        {
          label: 'Spend',
          value: indicatorsObj.spend,
          color: '#FF0000', // Red
          dasharray: '5,5',
          y: yScale(indicatorsObj.spend),
          x1: this.margins.left,
          x2: this.viewBoxWidth - this.margins.right,
          labelX: this.viewBoxWidth - this.margins.right - 175,
        },
        {
          label: 'Due',
          value: indicatorsObj.due,
          color: '#0000FF', // Blue
          dasharray: '2,2',
          y: yScale(indicatorsObj.due),
          x1: this.margins.left,
          x2: this.viewBoxWidth - this.margins.right,
          labelX: this.viewBoxWidth - this.margins.right - 125,
        },
        {
          label: 'Spend + Due',
          value: indicatorsObj.spendDue,
          color: '#FFA500', // Orange
          dasharray: '3,3',
          y: yScale(indicatorsObj.spendDue),
          x1: this.margins.left,
          x2: this.viewBoxWidth - this.margins.right,
          labelX: this.viewBoxWidth - this.margins.right - 75,
        },
      ];
    },
    getIndicatorsValues() {
      if (typeof this.indicators === 'string') {
        try {
          const data = JSON.parse(this.indicators);
          return [data.budget, data.spend, data.due, data.spendDue];
        } catch (e) {
          console.error('Invalid indicators JSON:', e);
          return [];
        }
      } else {
        return [this.indicators.budget, this.indicators.spend, this.indicators.due, this.indicators.spendDue];
      }
    },
    getBarColor(amount) {
      // Define your color logic here
      // For example, based on thresholds
      if (amount > 1500) return '#FF6384'; // High spend - Red
      if (amount > 1000) return '#36A2EB'; // Medium spend - Blue
      return '#4BC0C0'; // Low spend - Teal
    },
    showTooltip(bar, event) {
      this.tooltip.content = `${this.currency}${bar.amount}`;

      // Calculate tooltip position relative to the SVG
      const svg = this.$el.querySelector('svg');
      const svgRect = svg.getBoundingClientRect();
      const mouseX = event.clientX - svgRect.left;
      const mouseY = event.clientY - svgRect.top;

      // Convert mouseX and mouseY to percentages based on dynamic viewBox
      const percentX = (mouseX / svgRect.width) * 100;
      const percentY = (mouseY / svgRect.height) * 100;

      // Clamp percentages to prevent tooltip from overflowing
      const clampedX = Math.max(5, Math.min(percentX, 95));
      const clampedY = Math.max(5, Math.min(percentY, 95));

      this.tooltip.style.left = `${clampedX}%`;
      this.tooltip.style.top = `${clampedY}%`;
      this.tooltip.visible = true;
    },
    hideTooltip() {
      this.tooltip.visible = false;
    },
  },
};
</script>

<style scoped>
.bar-graph-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.bar-graph-svg {
  width: 100%;
  height: 100%;
}

.bars rect {
  transition: opacity 0.3s, transform 0.3s, height 0.5s, y 0.5s;
  cursor: pointer;
}

.bars rect:hover,
.bars rect:focus {
  opacity: 0.8;
  transform: scaleY(1.05);
  transform-origin: bottom;
}

.axes line {
  stroke: black;
  stroke-width: 1;
}

.y-ticks line {
  stroke: black;
  stroke-width: 0.5;
}

.y-ticks text {
  /* :font-size is bound dynamically */
  fill: black;
}

.x-ticks text {
  /* :font-size is bound dynamically */
  fill: black;
}

.indicators line {
  stroke-width: 0.5;
}

.indicators text {
  /* :font-size is bound dynamically */
  fill: black;
}

.legend rect {
  width: 10px;
  height: 10px;
  display: none;
}

.legend text {
  /* :font-size is bound dynamically */
  fill: black;
  display: none;
}

.tooltip {
  /* Styles are defined inline in the component's data */
  pointer-events: none;
  position: absolute;
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  border-radius: 4px;
  font-size: 3px; /* Consider removing or adjusting if needed */
  transform: translate(-50%, -120%);
  white-space: nowrap;
}
</style>
